import React from 'react';
import { Panel, useReactFlow, getRectOfNodes, getTransformForBounds } from 'reactflow';
import { toPng } from 'html-to-image';

function downloadImage(dataUrl) {
  
  console.log(dataUrl)
  const a = document.createElement('a');

  a.setAttribute('download', 'reactflow.png');
  a.setAttribute('href', dataUrl);
  a.click();
}

function saveObjectAsJsonFile(objectData, fileName) {
  const jsonData = JSON.stringify(objectData, null, 2);
  const blob = new Blob([jsonData], { type: 'application/json' });

  // Generate a temporary URL for the Blob
  const url = URL.createObjectURL(blob);

  // Create a temporary link element
  const link = document.createElement('a');
  link.href = url;
  link.download = fileName;

  // Append the link to the DOM and click it programmatically
  document.body.appendChild(link);
  link.click();

  // Remove the link from the DOM after the download starts
  window.requestAnimationFrame(() => {
    URL.revokeObjectURL(url);
    document.body.removeChild(link);
  });
}

function DownloadButton({nodes, edges}) {
  const { getNodes } = useReactFlow();

  const onClick = () => {
    // we calculate a transform for the nodes so that all nodes are visible
    // we then overwrite the transform of the `.react-flow__viewport` element
    // with the style option of the html-to-image library
    const nodesBounds = getRectOfNodes(getNodes());
    console.log(nodesBounds)
    const imageWidth = (nodesBounds.width - nodesBounds.x)*2;
    const imageHeight = (nodesBounds.height - nodesBounds.y)*2;
    const transform = getTransformForBounds(nodesBounds, imageWidth, imageHeight, 1, 2);

    toPng(document.querySelector('.react-flow__viewport'), {
      backgroundColor: 'rgb(225,225,225)',
      width: imageWidth,
      height: imageHeight,
      style: {
        width: imageWidth,
        height: imageHeight,
        transform: `translate(${transform[0]}px, ${transform[1]}px) scale(${transform[2]})`,
      },
    }).then(downloadImage);
  };

  const downloadJSON = () => {
    var object = {nodes: nodes, edges: edges, processed: true}
    saveObjectAsJsonFile(object, 'schametics_out')
  }

  if(!nodes[0]){return null}
  return (
    <Panel position="top-right" style={{margin: 0, padding: 9}}>
      <button className="download-btn" onClick={onClick} style={{marginRight: 5}}>
        Download Image
      </button>
      <button className="download-btn" onClick={downloadJSON}>
        Download JSON
      </button>
    </Panel>
  );
}

export default DownloadButton;
