var dagre = require("dagre");
var g = new dagre.graphlib.Graph();
g.setGraph({});
g.setDefaultEdgeLabel(function() { return {}; });

// Choose File button
function JsonFileInput({setRawData}) {
  const [fileContent, setFileContent] = useState("");

  function handleFileSelect(event) {
    const file = event.target.files[0];
    if(!file){return}
    const reader = new FileReader();
    reader.onload = () => {
        setRawData(reader.result);
    };
    reader.readAsText(file);
  }

  return (
    <Panel position={'top-center'} style={{margin: 0, padding: 0}}>
        <div style={{width: '100vw', height: 50, backgroundColor: 'teal', display: 'flex', alignItems: 'center', justifyContent: 'center', color: 'white' }}>
            <input type="file" onChange={handleFileSelect} />
            {fileContent && <p>File content: {fileContent}</p>}
        </div>
    </Panel>
  );
}



