const alignNodes = (nodeObjArray, edgeObjArray) => {
    // degre implementation

    nodeObjArray.forEach(item => g.setNode(item.id,    { label: item.id,  width: 100, height: 500 }))
    edgeObjArray.forEach(item => g.setEdge(item.source, item.target))
    dagre.layout(g);

    nodeObjArray = nodeObjArray.map(item => {
        item.position = {x:0, y: 0}
        item.position.y = g.node(item.id)["x"]
        item.position.x = g.node(item.id)["y"]
        return item
    })
    return nodeObjArray
}

 // remove outfalls
 const hideOutfallsFunc = (initialNodes, initialEdges) => {
    initialNodes = initialNodes.map(item => {item.hidden = item.outfall ? true : false; return item})
    // initialEdges = initialEdges.map(item => {if(initialNodes.findInde(node => node.id = item.target) || initialNodes.find(node => node.id = item.target) } )))
    return {nodes: initialEdges, edges: initialNodes}
}

